@import url(https://fonts.googleapis.com/css2?family=Lato&family=Satisfy&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0;box-sizing:border-box}body{font-size:14px}.App{text-align:center}

.page--coming-soon{height:100vh;width:100vw;padding:20px;background-color:black;background-image:url(/mitchpatterson/afraidknotropes/static/media/ropeBG.68ac4c16.jpeg);background-position:center;background-repeat:no-repeat;background-size:cover;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.page--coming-soon--content{width:600px;max-width:100%;height:400px;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;background-color:rgba(0,0,0,0.5);border:5px solid black}.page--coming-soon--content .spacer{height:3px;width:60%;background-color:white;margin:20px 0}.page--coming-soon--content h4{font-family:"Lato";color:white;font-size:1.5rem}.page--coming-soon--content h2{font-family:"Satisfy";font-size:4rem;color:white}.page--coming-soon--content p{color:white;font-family:"Lato";font-size:20px}.page--coming-soon--content .social-media{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;margin-top:30px}.page--coming-soon--content .social-media a{margin-right:20px;text-decoration:none;cursor:pointer}.page--coming-soon--content .social-media a:last-child{margin-right:0}.page--coming-soon--content .social-media a:active{outline:none}.page--coming-soon--content .social-media a svg{height:40px;width:40px;color:white;opacity:0.7;transition:all 0.5s ease}.page--coming-soon--content .social-media a:hover svg{opacity:1}

